@import 'variables.sass';

.content {
    flex: 3; }

.ui.secondary.buttons .button,
.ui.secondary.button {
  background-color: #fff;
  color: $black;
  text-shadow: none;
  background-image: none; }

.ui.secondary.buttons .button:hover, .ui.secondary.button:hover {
  background-color: mix( black, $gray, 10% );
  color: $black;
  text-shadow: none; }

.ui.secondary.buttons .button.active, .ui.secondary.button.active {
  background-color: $orange;
  color: #fff;
  text-shadow: none;
  background-image: none; }

.button--orange {
  background-color: $orange !important;
  color: #fff !important; }
.button--green {
  background-color: $green !important;
  color: #fff !important;
  font-size: 24px !important;
  margin-top: 30px !important;
  padding: 15px;
  font-family: 'IntroRegular' !important;
  -webkit-box-shadow: 0px 0px 0px 7px rgba(255, 255, 255, 0.3) !important;
  -moz-box-shadow:    0px 0px 0px 7px rgba(255, 255, 255, 0.3) !important;
  box-shadow:         0px 0px 0px 7px rgba(255, 255, 255, 0.3) !important;
  &:hover {
    color: $green-d !important;
    -webkit-box-shadow: 0px 0px 0px 0px rgba(255, 255, 255, 0.3) !important;
    -moz-box-shadow:    0px 0px 0px 0px rgba(255, 255, 255, 0.3) !important;
    box-shadow:         0px 0px 0px 0px rgba(255, 255, 255, 0.3) !important; } }


.modal-close {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  background-color: rgba(10, 10, 10, 0.2);
  border: none;
  border-radius: 290486px;
  cursor: pointer;
  display: inline-block;
  -webkit-box-flex: 0 {
      -ms-flex-positive: 0 {
          flex-grow: 0; } }
  -ms-flex-negative: 0 {
      flex-shrink: 0; }
  font-size: 20px;
  height: 20px;
  max-height: 20px;
  max-width: 20px;
  min-height: 20px;
  min-width: 20px;
  outline: none;
  position: relative;
  vertical-align: top;
  width: 20px;
  background: none;
  height: 40px;
  position: fixed;
  right: 20px;
  top: 20px;
  width: 40px; }


.modal-close:before, .modal-close:after {
  background-color: rgb(236, 240, 241) !important;
  content: "<  назад в каталог";
  display: block;
  left: 120%;
  position: absolute;
  top: 13%;
  font-size: 16px;
  width: 180px;
  padding: 0 0 0 10px;
  font-weight: normal; }


.modal-close:hover, .modal-close:focus {
  background-color: rgba(10, 10, 10, 0.3); }


.modal-close:active {
  background-color: rgba(10, 10, 10, 0.4); }


.modal-close.is-small {
  height: 16px;
  width: 16px; }


.modal-close.is-medium {
  height: 24px;
  width: 24px; }


.modal-close.is-large {
  height: 32px;
  width: 32px; }


.modal-card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal {
      -ms-flex-direction: column {
          flex-direction: column; } }
  max-height: calc(100vh - 40px);
  overflow: hidden; }


.modal-card-head,
.modal-card-foot {
  -webkit-box-align: center {
      -ms-flex-align: center {
          align-items: center; } }
  background-color: whitesmoke;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 0 {
      flex-shrink: 0; }
  -webkit-box-pack: start {
      -ms-flex-pack: start {
          justify-content: flex-start; } }
  padding: 20px;
  position: relative; }


.modal-card-head {
  border-bottom: 1px solid #dbdbdb;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px; }


.modal-card-title {
  color: #363636;
  -webkit-box-flex: 1 {
      -ms-flex-positive: 1 {
          flex-grow: 1; } }
  -ms-flex-negative: 0 {
      flex-shrink: 0; }
  font-size: 1.5rem;
  line-height: 1; }


.modal-card-foot {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border-top: 1px solid #dbdbdb; }


.modal-card-foot .button:not(:last-child) {
  margin-right: 10px; }


.modal-card-body {
  -webkit-overflow-scrolling: touch;
  background-color: white;
  -webkit-box-flex: 1 {
      -ms-flex-positive: 1 {
          flex-grow: 1; } }
  -ms-flex-negative: 1 {
      flex-shrink: 1; }
  overflow: auto;
  padding: 20px; }

.modal {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-box-align: center {
      -ms-flex-align: center {
          align-items: center; } }
  display: none;
  -webkit-box-pack: center {
      -ms-flex-pack: center {
          justify-content: center; } }
  overflow: hidden;
  position: fixed;
  z-index: 20; }


.modal.is-active {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }



